import { Link } from 'react-router-dom';

function UserNavigation(): JSX.Element {
  return (
    <nav className="main-nav">
      <ul className="main-nav__list">
        <li>
          <Link className="link main-nav__link" to="/"></Link>
        </li>
        <li>
          <Link className="link main-nav__link" to="#"> ?</Link>
        </li>
        <li>
          <Link className="link main-nav__link" to="#"> </Link>
        </li>
      </ul>
    </nav>
  );
}

export default UserNavigation;
