function Sort(): JSX.Element {
  return (
    <div className="catalog-sort">
      <h2 className="catalog-sort__title">:</h2>
      <div className="catalog-sort__type">
        <button className="catalog-sort__type-button" aria-label=" "> </button>
        <button className="catalog-sort__type-button" aria-label=" "> </button>
      </div>
      <div className="catalog-sort__order">
        <button className="catalog-sort__order-button catalog-sort__order-button--up" aria-label=" "></button>
        <button className="catalog-sort__order-button catalog-sort__order-button--down" aria-label=" "></button>
      </div>
    </div>
  );
}

export default Sort;
